<script>
import { ref } from "@vue/reactivity";
import request, { auth, headers } from "../../../network/request";

export default {
  data() {
    return {
      selectcat: "请选择",
      feed_detail: "",
      name: "",
      phone: "",
      mail: "",
      toast: "",
      types: [
        {
          id: 2,
          name: "业务逻辑",
        },
        {
          id: 3,
          name: "用户体验",
        },
      ],
      subSuc: false,
    };
  },
  methods: {
    submitSucceeded() {
      console.log("suc");
    },
    async submitFeedback(e) {
      // console.log(this.$toast)
      let data = {
        category: this.selectcat,
        content: this.feed_detail,
        name: this.name,
        mobile: this.phone,
        email: this.mail,
      };
      console.log("data", data);
      let res = await request({
        method: "POST",
        url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/base/funcs/send/provider/feedback/email",
        data,
        auth,
        headers,
      });

      console.log(res);
      if (res.Code == 200) {
        this.$toast.success("已提交成功", 200, 400, 3000);
        this.submitSucceeded();
      } else {
        this.$toast.warn("提交出错", 200, 400, 3000);
      }
    },
  },
};
</script>
